import React from 'react';

function StoregCP() {

  //  / 
  // 
  localStorage.setItem('age', '20');
  sessionStorage.setItem('age', '20');
  // 
  localStorage.getItem('age');
  sessionStorage.getItem('age');
  // 
  localStorage.removeItem('age');
  sessionStorage.removeItem('age');

  //array / object 
  let obj = {name : 'kim'}
  localStorage.setItem('data', JSON.stringify(obj)); //JSON.stringify() JSON  
  
  

  let dataGet = localStorage.getItem('data'); // array / object 
  JSON.parse(dataGet); // JSON.parse() JSON 
  console.log(dataGet);

  //  arr/obj push  
  //obj
  let storegData = JSON.parse(localStorage.getItem('data'));
  storegData.Phone = '1234-5678';
  console.log(storegData);

  //arr
  let arr = [1,2,3,4];
  localStorage.setItem('arrEx', JSON.stringify(arr));

  let arrGet = JSON.parse(localStorage.getItem('arrEx'));
  arrGet.push(5,6,7);
  console.log(arrGet);

  return (
    <>
    <h4>storeg </h4>
    </>
  );
}
export default StoregCP;
